export const getLanguageName = function(key: string| null) {
    if (!key) {
        return "Unknown";
    }
    key = key.slice(0,2);
    const lang = isoLangs[key];
    return lang ? lang.name : undefined;
}
export const getLanguageNativeName = function(key: string | null) {
    if (!key) {
        return "Unknown";
    }
    key = key.slice(0,2);
    const lang = isoLangs[key];
    return lang ? lang.nativveName : undefined;
}